@import url(https://fonts.googleapis.com/css2?family=Unna&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
    margin: 0;
    padding: 0;
    font-family: 'Unna', serif;
}
#container {
    width: 100%;
    height: 800px;
}
.header {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
}
.header_logo {
    position: fixed;
    top: 5%;
    left: 3%;
}
.header_title {
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    padding-top: 20px;
    font-size: 40px;
    letter-spacing: 36px;
    color: #212121;
}
.header_content h4 {
    font-size: 32px;
    color: #616161;
    letter-spacing: 2px;
}
.header_content h4:nth-child(1) {
    position: absolute;
    top: 33%;
    left: 45%;
    -webkit-transform: translateX(-45%);
            transform: translateX(-45%);
}
.header_content h4:nth-child(2) {
    position: absolute;
    top: 37%;
    left: 54%;
    -webkit-transform: translateX(-54%);
            transform: translateX(-54%);
}

a {
    text-decoration: none;
    color: #6a6a6a;
}
@font-face {
    font-family: 'MapoFlowerIsland';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/MapoFlowerIslandA.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
* {
    padding: 0;
    margin: 0;
}
.profile_image h1 {
    margin-bottom: 10px;
    color: #424242;
    font-size: 24px;
    text-align: center;
    letter-spacing: 2px;
}
.profile_image {
    padding-top: 40px;
    padding-bottom: 150px;
    text-align: center;
    position: relative;
}
.profile_image h2 {
    font-size: 30px;
    padding-bottom: 10px;
    letter-spacing: 10px;
    color: #212121;
}
.profile_image h2 span {
    font-size: 21px;
}
.profile_image img {
    width: 300px;
}
.profile_image h3 {
    letter-spacing: 20px;
    color: #212121;
}
.profile_moto h4 {
    font-family: 'MapoFlowerIsland';
    font-size: 50px;
    color: silver;
    opacity: 0.2;
}
.profile_moto h4:nth-child(1) {
    position: absolute;
    top: 25%;
    left: 20%;
    -webkit-transform: translateX(-20%);
            transform: translateX(-20%);
}
.profile_moto h4:nth-child(2) {
    position: absolute;
    top: 30%;
    right: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
hr {
    width: 100px;
    color: gray;
    margin: 0 auto;
}
.profile {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 160px 0;
    color: #424242;
    line-height: 32px;
}
.profile_study {
    -webkit-align-self: center;
            align-self: center;
}
.profile_study span {
    font-size: 12px;
}
.profile_skill {
    font-size: 18px;
    font-weight: bold;
}
.profile_skill span {
    font-size: 14px;
    padding-right: 8px;
}
.container_box img {
    margin: 40px 0;
    width: 400px;
    height: 100%;
}
.container_box button {
    width: 100px;
    background: none;
    padding: 8px;
    margin: 10px 20px;
    cursor: pointer;
    transition: all .3s;
}
.container_box .link_btn i {
    padding-right: 7px;
}
.container_box .link_btn:hover {
    background: #000;
    color: #fff;
}
.container_box button:focus {
    border: none;
    outline: none;
}

.container_box {
    width: 90%;
    height: 100%;
    margin: 40px auto;
    text-align: center;
    color: #212121;
    font-family: 'Oswald', sans-serif;
}
.project {
    margin-bottom: 100px;
}
.project_title:after {
    content: "";
    display: block;
    clear: both;
}
.project_title h2 {
    width: 30%;
    padding: 17px 0 20px 0;
    border-bottom: 1px solid darkgray;
    border-top: 1px solid darkgray;
    font-size: 24px;
    letter-spacing: 8px;
    text-shadow: 1px 1px 1px silver;
}
.project_text h2 {
    padding: 10px 0;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 3px;
}
.project_text h3 {
    padding: 5px 0;
    font-size: 14px;
    color: #424242;
    letter-spacing: 1px;
}
.project_text p {
    color: #616161;
    padding: 20px 0;
    line-height: 24px;
    font-size: 12px;
} 
.lang_btn_box {
    margin-top: 18px;
}
.lang_btn {
    border: none;
    font-size: 10px;
    width: 50px;
    color: #3a3a3a;
}
.lang_btn:hover {
    text-decoration: underline;
    background: none;
    color: black;
}

.wrap_box {
    width: 100%;
    height: 800px;
    background-image: url(/portfolio/static/media/img_myself.163b82a3.jpg);
    background-repeat: no-repeat;
    background-size: 50%;
    background-position: 20%;
    position: relative;
}
.contact_box {
    width: 30%;
    height: 50%;
    padding: 3% 2%;
    position: absolute;
    top: 50%;
    left: 80%;
    -webkit-transform: translate(-80%,-50%);
            transform: translate(-80%,-50%);
    text-align: center;
    background: #fff;
}
.bold_line {
    width: 80%;
    border-bottom: 5px solid #000;
    margin-bottom: 50px;
    margin-top: 10px;
}
.text_email, .text_tel, .text_address {
    width: 100%;
    height: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    margin-bottom: 45px;
    color: #2e2e2e;
    border-bottom: 1px solid rgb(69, 69, 69);
    -webkit-align-items: center;
            align-items: center;
}
.contact_box i {
    padding: 0 10px;
}
.text_email a, .text_tel p {
    margin-left: auto;
    margin-right: 1%;
}
.text_email button {
    background: none;
    border: none;
    cursor: pointer;
    color: #2e2e2e;
}
.text_address p {
    font-size: 12px;
    margin-left: auto;
    margin-right: 1%;
}
.logo {
    padding-top: 20px;
}
